<script setup lang="ts">
import { useIdentityById } from "~/queries/identities";

const route = useRoute();
const identityId = route.params.mainId as string;
const searchValue = route.query.q as string;
const isMobile = useIsMobile();

const { data: identity, isLoading } = useIdentityById(() => identityId);

const banners = computed(() => {
  if (isMobile.value && identity.value?.mobileBanners && identity.value.mobileBanners.length > 0) {
    return identity.value.mobileBanners;
  }

  return identity.value?.banners;
});

useHead({ title: computed(() => identity.value?.title as string) });
definePageMeta({ layout: "home" });
</script>

<template>
  <div v-if="isLoading" class="skeletonLoading w-full h-full aspect-[2/1] md:aspect-[4/1] lg:aspect-[24/6]" />
  <div v-else-if="banners?.length === 1" class="w-full h-full max-h-[467px] overflow-hidden flex">
    <BaseBanner
      :banner="banners[0]"
    />
  </div>

  <div v-else-if="banners && banners.length > 1" class="w-full aspect-[2/1] md:aspect-[4/1] lg:aspect-[22/5] overflow-hidden flex">
    <BaseSlider
      :slides="banners"
      has-autoplay
      has-controls
      has-indicator
      slide-image-class="object-fit"
      slide-height="auto"
      slide-width="full"
    />
  </div>

  <ListIdentityItems
    class="main-container mb-12"
    has-load-more-button
    :identity-id="identityId"
    :search-value="searchValue"
    link-list
  >
    <template #start>
      <div class="font-bold text-2xl text-main mb-6 mt-10">
        {{ identity?.title }}
      </div>
    </template>
  </ListIdentityItems>
</template>
